* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: Arial, Helvetica, sans-serif;
  font-size: calc(10px + 2vmin);
  min-height: 100vh;
  width: 100%;
  z-index: -9999;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: none; }

.background-wrapper {
  width: 100%;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: -999;
  background: -webkit-gradient(linear, left bottom, right top, from(#2b50c5), to(#607edc));
  background: linear-gradient(to top right, #2b50c5, #607edc); }
  .background-wrapper svg {
    width: 100%; }

.card-container {
  margin: 20px auto;
  z-index: 10;
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #dc607e;
  box-shadow: 10px 15px 20px #242424;
  border: 20px solid #dcbe60;
  width: 90%; }
  @media screen and (min-width: 601px) {
    .card-container {
      width: 50%;
      min-width: 600px; } }
  .card-container .card {
    display: flex;
    flex-direction: column;
    align-content: center;
    text-align: center;
    width: 100%;
    min-height: 80vh; }
    .card-container .card h1 {
      font-family: "Lobster", cursive;
      font-size: 84px;
      color: #dcbe60; }

h2 {
  font-family: "Coming Soon", cursive;
  font-size: calc(0.5rem + 2vmin); }

input {
  display: inline-block;
  margin: 10px auto;
  font-family: "Coming Soon", cursive;
  font-size: calc(0.3rem + 2vmin);
  border: none;
  border-bottom: 2px solid #2b50c5;
  color: #000;
  background-color: transparent;
  text-align: center;
  padding: 10px;
  width: 90%; }
  input::-webkit-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #000;
    opacity: 1;
    /* Firefox */ }
  input::-moz-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #000;
    opacity: 1;
    /* Firefox */ }
  input:-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #000;
    opacity: 1;
    /* Firefox */ }
  input::-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #000;
    opacity: 1;
    /* Firefox */ }
  input::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #000;
    opacity: 1;
    /* Firefox */ }
  input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #000; }
  input::-ms-input-placeholder {
    /* Microsoft Edge */
