  input[type="text"] {
    width: 45%; }
  input[type="number"] {
    width: 30%; }
  input[type="range"] {
    display: block;
    width: 50%;
    margin: 10px auto; }
  input[name="name"] {
    width: 90%;
    display: block;
    border: none;
    background-color: transparent;
    color: #dcbe60;
    font-size: calc(1rem + 8vmin);
    font-family: "Lobster", cursive;
    margin-bottom: 60px;
    padding-bottom: 5px; }
    input[name="name"]::-webkit-input-placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: #dcbe60;
      opacity: 1;
      /* Firefox */ }
    input[name="name"]::-moz-placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: #dcbe60;
      opacity: 1;
      /* Firefox */ }
    input[name="name"]:-ms-input-placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: #dcbe60;
      opacity: 1;
      /* Firefox */ }
    input[name="name"]::-ms-input-placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: #dcbe60;
      opacity: 1;
      /* Firefox */ }
    input[name="name"]::placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: #dcbe60;
      opacity: 1;
      /* Firefox */ }
    input[name="name"]:-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: #dcbe60; }
    input[name="name"]::-ms-input-placeholder {
      /* Microsoft Edge */
      color: #dcbe60; }
    input[name="name"]:focus, input[name="name"]:active {
      -webkit-transition: 0.5s ease;
      transition: 0.5s ease;
      outline: none;
      border-bottom: 3px solid #dcbe60; }
  @media screen and (min-width: 601px) {
