      outline: none; } }

label {
  font-family: "Lobster", cursive;
  color: #2b50c5;
  font-size: calc(1rem + 4vmin);
  margin-bottom: 0;
  padding-bottom: 0; }

button {
  display: inline-block;
  justify-self: center;
  align-self: center;
  font-size: 16px;
  padding: 10px 20px;
  margin-top: 2rem;
  border: 2px solid #2b50c5;
  color: #ffffff;
  background: #2b50c5;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease; }
  button:hover, button:active {
    cursor: pointer;
    background-color: #2b50c5;
    color: #ffffff;
    box-shadow: 0 3px 10px #242424; }

footer {
  width: 100%;
  text-align: center;
  position: relative;
  bottom: 0;
  left: 0; }
  footer a {
    text-decoration: underline;
    color: #2b50c5; }
  footer p {
    display: inline-block;
    padding: 10px;
    background-color: #dcbe60; }

